<template>
    <div>
        <vbl-radio v-model="val" native-value="0"></vbl-radio>
        <vbl-radio v-model="val" native-value="1"></vbl-radio>
        <vbl-radio v-model="val" native-value="2" disabled></vbl-radio>
        
        <br/>
        <br/>
        {{val}}
    </div>
</template>

<script>
    export default {
        data() {
            return {
                val: '0'
            };
        }
    }
</script>


