<template>
    <div>
        <vbl-modal :show.sync="show">
            <p></p>
        </vbl-modal>
        <vbl-modal :show.sync="show1" vertical-center>
            <p></p>
        </vbl-modal>
        <vbl-button @click="show = true"></vbl-button>
        <br/><br/>
        <vbl-button @click="show1 = true"></vbl-button>
        <br/><br/>
        <vbl-button @click="showImg"></vbl-button>
        <br/><br/>
        <vbl-button @click="showConfirm"></vbl-button>
        <br/><br/>
        <vbl-button @click="showAlert"></vbl-button>
    </div>
</template>

<script>
import imgSrc from './2.jpg';

export default {
    data() {
        return {
            show: false,
            show1: false
        }
    },
    methods: {
        showConfirm() {
            this.$modal.confirm(
                {
                    content: '?',
                    okCallback: () => {
                        console.log('ok');
                    },
                    cancelCallback: () => {
                        console.log('cancel');
                    }
                }
            );
        },
        showAlert() {
            this.$modal.alert('');
        },
        showImg() {
            this.$modal.img({
                src: imgSrc
            });
        }
    }
}
</script>



