<template>
    <div>
        <vbl-button @click="showInfo"></vbl-button>
        <br/><br/>
        <vbl-button @click="showError"></vbl-button>
        <br/><br/>
        <vbl-button @click="showSuccess"></vbl-button>
        
    </div>
</template>

<script>
    export default {
        methods: {
            showInfo() {
                this.$alert.info('');
            },
            showError() {
                this.$alert.error('');
            },
            showSuccess() {
                this.$alert.success('');
            },
        }
    }
</script>


