<template>
    <div>
        <vbl-tab-group :selected-index.sync="index">
            <vbl-tab label="1">1</vbl-tab>
            <vbl-tab label="2">2</vbl-tab>
            <vbl-tab label="3">3</vbl-tab>
        </vbl-tab-group>
    </div>
</template>

<script>
export default {
    data() {
        return {
            index: 0
        }
    }
};
</script>



