<template>
    <div>
        <p>
            
            <vbl-select v-model="val1">
                <vbl-option :value="true"></vbl-option>
                <vbl-option :value="false"></vbl-option>
            </vbl-select>
        </p>
        {{val1}}
        <br/>
        <br/>
        <p>
            
            <vbl-select v-model="val">
                <vbl-option value="0"></vbl-option>
                <vbl-option value="1"></vbl-option>
                <vbl-option value="2" disabled></vbl-option>
            </vbl-select>
        </p>
        {{val}}
        <br/>
        <br/>
        <p>
            
            <vbl-multi-select v-model="valArr">
                <vbl-multi-option value="0"></vbl-multi-option>
                <vbl-multi-option value="1"></vbl-multi-option>
                <vbl-multi-option value="2"></vbl-multi-option>
                <vbl-multi-option value="3"></vbl-multi-option>
                <vbl-multi-option value="4"></vbl-multi-option>
                <vbl-multi-option value="5" disabled></vbl-multi-option>
            </vbl-multi-select>
        </p>
        {{valArr}}
        <br/>
        <br/>
        <p>
            
            <vbl-select v-model="val" disabled>
                <vbl-option value="0"></vbl-option>
                <vbl-option value="1"></vbl-option>
                <vbl-option value="2" disabled></vbl-option>
            </vbl-select>
        </p>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                val1: false,
                val: '',
                valArr: []
            };
        }
    }
</script>



